.footer__backdrop {
  display: block;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(36, 29, 29, 0.4);
}

.footer__modal {
  background-color: white;
  margin: 0 auto;
  padding: 1px;
  border: 1px solid rgb(141, 65, 65);
  border-radius: 20px;
  width: 70%;
  position: relative;
}

.close {
    width: 30px;
    height: 30px;
    background-color: transparent;
    border: 2px solid rgba(23, 22, 22, 0.547);
    border-radius: 50%;
    margin-right: 10px;
    margin-top: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.close:hover,
.close:focus {
  color: rgb(238, 9, 9);
  border-color: rgb(238, 9, 9);;
  text-decoration: none;
  cursor: pointer;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}


.team__list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.team__item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  background-color: transparent;

