.team__wrapper {
  display: flex;
  align-items: center;
}

.sns-list {
  margin-left: auto;
}

.sns-list__item {
  display: block;
  margin: 0 auto;
}

.sns-list__link {
  display: flex;
  align-items: center;
  justify-content: center;

  margin-bottom: 10px;
  margin-left: 10px;
  border: none;
  outline: none;

  width: 100%;
  cursor: pointer;

  width: 24px;
  height: 24px;
  border-radius: 20px;
  background-color: transparent;
  box-shadow: -2px -2px 5px white, 3px 3px 5px rgba(0, 0, 0, 0.1);
}

@keyframes text {
  0% {
    color: var(--mainAccentColor);
    margin-bottom: 10px;
  }

  30% {
    letter-spacing: 25px;
    margin-bottom: 10px;
  }

  85% {
    letter-spacing: 10px;
    margin-bottom: 10px;
  }
} 

.team__student-photo--wrap {
  margin: 0 auto;
  padding: 20px;
}

.team__border-outside {
  padding: 5px;
  margin: 0 auto;
  width: 150px;
  border-radius: 10%;
  border: none;
  margin-bottom: 10px;
}

.team__foto-thumb {
  margin-bottom: 0;
  width: 140px;
  height: 170px;
  border-radius: 10%;
  overflow: hidden;
}

.team__student-foto {
  width: 100%;
  height: 100%;
  margin-bottom: 0;
  object-fit: cover;
  object-position: top;
}

.team__student-card {
padding: 10px;
  font-size: 14px;
}

.team__student-name {
  margin-bottom: 10px;
  font-size: 15px;
}

.team__student-role {
  color: grey;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 15px;
  text-shadow: 0.5px 0.5px 1px rgb(255, 255, 255);
}

.noScroll {
  overflow: hidden;
}
